import { DOCUMENT } from '@angular/common';
import { Component, ElementRef, HostListener, Inject, Input, OnInit, Renderer2 } from '@angular/core';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {
  @Input() downloadLink: string;
  @Input() firefoxClass: string;

  showMenu: boolean = false;
  selectedItem = null;
  constructor(@Inject(DOCUMENT) private document: Document) { }

  ngOnInit(): void {
  }
  @HostListener('window:scroll', [])
  onWindowScroll() {
    if (document.body.scrollTop > 20 ||
      document.documentElement.scrollTop > 20) {
      document.getElementById('navbar').classList.add('scroll-header');
    } else {
      document.getElementById('navbar').classList.remove('scroll-header');
    }
    const sections = document.querySelectorAll("section[id]");

    // Get current scroll position
    let scrollY = window.pageYOffset;

    // Now we loop through sections to get height, top and ID values for each
    sections.forEach(current => {
      const sectionHeight = current['offsetHeight'];
      const sectionTop = current['offsetTop'] - 50;
      let sectionId = current.getAttribute("id");

      /*
      - If our current scroll position enters the space where current section on screen is, add .active class to corresponding navigation link, else remove it
      - To know which link needs an active class, we use sectionId variable we are getting while looping through sections as an selector
      */
      if (
        scrollY > sectionTop &&
        scrollY <= sectionTop + sectionHeight
      ) {
        document.querySelector(".navigation a[id*=" + sectionId + "]").classList.add("active");
      } else {
        document.querySelector(".navigation a[id*=" + sectionId + "]").classList.remove("active");
      }
    });

  }
  scroll(id) {
    let el = document.getElementById(id);
    const position = (el.getBoundingClientRect().top + window.scrollY) - 80;
    window.scrollTo({
      top: position,
      left: 100,
      behavior: 'smooth'
    });

  }
}
