import { Component, OnInit } from '@angular/core';
import AOS from 'aos';
@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {
  downloadLink = "https://chrome.google.com/webstore/detail/koalament/igecidcneciljgfkimafpbhkhmekadea";
  firefoxClass: string = null;

  constructor() { }

  ngOnInit(): void {
    AOS.init();
    var ua = navigator.userAgent.match(/(opera|chrome|safari|firefox|msie)\/?\s*(\.?\d+(\.\d+)*)/i),
      browser;
    if (navigator.userAgent.match(/Edge/i) || navigator.userAgent.match(/Trident.*rv[ :]*11\./i)) {
      browser = "msie";
    } else {
      browser = ua[1].toLowerCase();

    }
    if (browser == 'firefox') {
      this.firefoxClass = 'firefox';
      this.downloadLink = 'https://addons.mozilla.org/en-US/firefox/addon/koalament/';
    } else if (browser == 'chrome') {
      this.downloadLink = 'https://chrome.google.com/webstore/detail/koalament/igecidcneciljgfkimafpbhkhmekadea';
    }

  }

}
